@import url(./glitch.css);
@font-face {
  font-family: "az2"; /* Nombre que usarás para referenciar la fuente */
  src: url("../experience//font//a2z.ttf") format("truetype"); /* Ruta al archivo TTF y formato */
  /* Puedes agregar más formatos si la fuente también está disponible en otros formatos como WOFF o WOFF2 */
}
body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
canvas{
  filter: brightness(1.1) saturate(1.45);
}

:root {
  --opacity: 0;
  --position: absolute;
  --anim-state: paused;
  --span-color: black;
  --img: invert();
  --size: 0;
  --index: -1;
  --start-btn-scale: 1;
}
#renderer{
  transition: all 1s;
}
.dark{
  transition: all 1s;
  background-color: rgba(0, 0, 0, 0.3);
}
.fade-in {
  animation: fadeIn 2s ease-in forwards;
}
.fade-out {
  animation: fadeOut 2s ease-in forwards;
}

.deactive {
  display: none !important;
}
.no-visible {
  transition: 1.4s;
  opacity: 0;
}

.element {
  width: fit-content;
  height: auto;
  padding: 15px;
  background-color: transparent;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transform: scale(1);
  transition: all 0.7s;
}
.element.desactive {
  visibility: hidden;
}
.element-b {
  color: rgb(147, 239, 255);
  display: flex;
  align-items: center;
  padding: 10px 20px;
  border-radius: 0.3rem;
  font-size: 35px;
  transition: all 0.7s;
  visibility: hidden;

  background: rgba(255, 255, 255, 0.059);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 2px solid rgb(147, 239, 255);

  -webkit-box-shadow: 0px 0px 25px 1px rgba(99, 232, 255, 0.3);
  -moz-box-shadow: 0px 0px 25px 1px rgba(99, 232, 255, 0.3);
  box-shadow: 0px 0px 25px 1px rgba(99, 232, 255, 0.3);
}
#overlay {
  width: 100vw;
  height: 100vh;
  position: absolute;
}
.active {
  opacity: 1 !important;
  visibility: visible !important;
  transform: scale(1); /* Escala inicial */
  transition: transform 1.5s, opacity 1.5s; /* Agregar transición a transform y opacity */
}
.not-active {
  opacity: 0 !important;
  transform: scale(0); /* Escalar el elemento a 0 */
  transition: all 0.8s cubic-bezier(1, 0, 0.9, 0.4);
}
.element-b:hover {
  transition: all 0.7s;

  transform: scale(1.1);
}
.element:hover {
  color: rgb(255, 255, 255);
  overflow: hidden;
  transition: all 0.7s;
  transform: scale(1.1);
  --position: relative; /* Oculta y saca del flujo de diseño */
  --anim-state: running;
  --opacity: 1;
  --span-color: black;
  --size: 18px;
  --index: 2;
}
.element span {
  font-family: az2;
  text-align: center;
  color: white;
}
.element img {
  padding: 10px;
  margin-bottom: 8px;
  width: 120px;
  height: auto;
  z-index: 3;
}
.element h2 {
  font-family: az2;

  text-align: center;
  color: white;
  padding: 0;
  margin: 0;
  margin: 0;
  transition: all 0.7s;
  pointer-events: none; /* Evita que el contenido sea interactivo durante la animación */
}
@keyframes round {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/*Animacion de inicio
/* Definición de la animación */
/* Definición de la animación */
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

/*Drag Drop effects*/
.dg.ac {
  z-index: 100 !important ;
  display: none;
}

#cortain {
  background-color: transparent;
  position: fixed;
  width: 100vw;
  height: 100vh;
}
.cortain {
  background-color: black;
  position: fixed;
  z-index: 100;
  height: 0vh;
  width: 100%;

  transition: all 2s;
  background: linear-gradient(
    to bottom,
    transparent 0%,
    rgba(0, 0, 0, 0.1) 5%,
    rgb(0, 0, 0) 100%
  );
}
.cortain__top {
  top: 0;
  transform: rotate(180deg);
}
.cortain__bottom {
  bottom: 0;
}
.active-cortain {
  height: 20vh;
}
/*ROOT*/
.root {
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 99;
  position: fixed;
  display: grid;
  place-content: center;
  align-items: center;
}

/*BANNERS*/
.navbar {
    
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: grid;
  place-content: center;
}
.navbar__logo {
    height: 60px;
    width:  60px;
    margin: 25px;
    background-image: url(../experience/img/logo.png);
    background-repeat: no-repeat;
    background-size: cover;
}
.navbar__back-btn{
  position: absolute;
  height: 60px;
  width: 120px;
  z-index: 100;
  top: 15px;
  left: 25px;
  background-image: url(../experience/img/back-btn.png);
  background-size: cover;
  transition: all .5s;
}
.navbar__back-btn:hover{
  transition: all .5s;
  transform: scale(1.05);
}


.footer {
  position: fixed;
  height: 80px;
  bottom: 0;
  left: 0;
  right: 0;
  color: #aaaaaa;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 100;
}
.footer__action-btn {
  height: 40px;
  width:  120px;
  margin: 0 30px;
  border:  none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: contain;
}
.footer__text {
    font-family: az2;
    font-weight: 150;
}
.footer--hide-footer-btn {
    background-image: url("../experience/img/hide-footer-btn.png");
    background-position: left;

}
.footer--sound-btn {
    padding: 10px 0;
    background-image: url("../experience/img/anim/sound-btn.gif");
    background-position: right;
    position: relative;
}

#sound-btn__span{
  position: absolute;
  padding-top: 2px;
  top: 50%;
  transform: translate(0, -50%);
  color: #15eded;
  text-align: center;
  right: 15px;
}

.footer #navigation-menu{
  width: fit-content;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
  z-index: 0;
}
.navigation-menu__btn-container{
  height: auto;
  margin: 0, 15px;
}
.btn-container__icon{
  height: 50px;
  width: auto;
  position: relative;
  z-index: 10;
}

/*END BANNERS*/

/*LOAD SCREEN*/
.load-screen {
  width: 70vw;
  height: 70vh;
  display: grid;
  padding: 15vh 15vw;
  place-content: center;
  position: relative;
}
.load-screen__logo{
  position: absolute;
  top: 25px;
  left: 50%;
  transform: translate(-50%, 0);
  margin: 0 !important;
}
.load-screen__span{
  font-family: az2;
  font-weight: 150;
  font-size: 0.8rem;
  color: #aaa;
  text-align: center;
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translate(-50%, 0);
}

.action-container {
  width: 100%;
  height: 100%;
}
.action-container__start-btn {
  background: transparent;
  border: none;
  width: 260px;
  height: 260px;
  padding: 5px;
}
.start-btn__text {
  font-family: az2;
  font-weight: 250;
  color: white;
  font-size: 1.5em;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.start-btn__icon {
  filter: opacity(0.5);
  width: 100%;
  height: 100%;
  background-image: url("../experience/img/inner_being_icon.png");
  background-repeat: no-repeat;
  background-size: contain;
  position: relative;
  transition: .5s all;

}
.start-btn__icon:hover{
  transform: scale(0.9);
  transition: 1.5s all;
}
.start-btn__icon::after{
  width:  120%;
  height: 120%;

  background-image: url("../experience/img/outter_begin_icon.png");
  background-repeat: no-repeat;
  background-size: contain;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: rotate360 infinite 15s linear;
}

@keyframes rotate360 {
  to {
    transform:translate(-50%, -50%) rotate(360deg);
  }
}


/*END LOAD-SCREEN*/

/*FIRST SCREE*/
.first-screen {
  font-family: az2;
  font-weight: 300;
}
.first-screen__intro-container {
  color: white;
  width: fit-content;
  padding: 80px 3.5em;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.intro-container__title {
  font-weight: 600;
  font-size: 5.5em;
  line-height: 0.9;
  margin-bottom: 30px;
}
.intro-container__sub-title {
  margin-bottom: 20px;
  font-size: 1.2em;
}
.intro-container__control-panel {
  display: flex;
  width: 60%;
  justify-content: space-evenly;
  align-items: center;
  padding: 30px 15px;
}

.intro-container__control-panel button:not(:hover){
  opacity: 0.8;
  transition: all 0.8s;
}
.intro-container__control-panel button:hover{
  transform: scale(1.05);
  transition: all 0.8s;
}


.control-panel__intro-btn {
  width: fit-content;
  height: 55px;
  border: none;
  border-radius: 1em;
  color: white;
  font-size: 1em;
  background-color: transparent;
  position: relative;

  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 0 10px;
}
.control-panel__intro-btn *{
  pointer-events: none;
  
}

.control-panel__intro-btn--features-btn {
  background: linear-gradient(90deg, rgba(0, 118, 255, 0.80) 0.1%, rgba(0, 0, 0, 0.00) 90%);
  
}
.control-panel__intro-btn--features-btn:hover {
}

.control-panel__intro-btn--launch-app-btn {
  background-image: url("../experience/img/intro-btn__launch-app.png");
}
.intro-btn__text {
  margin-right: 15px;
  margin-left: 25px;
}
.intro-btn__icon {
  width: 30px;
  height: 30px;
  background-image: url("../experience/img/intro-btn__icon.png");
  background-size: cover;
  background-repeat: no-repeat;
  padding: 5px;
}
.control-panel__btn {
}

/*END FIRST*/

/*MAIN CONTENT*/

.swiper {
  width: 60vw;
  height: 620px;
}
.swiper-pagination{
  margin-top: 80px;
}
span.swiper-pagination-bullet {
  color: #fff;
  outline: solid 1px #15EDED;
  text-align: center;
  padding: auto;
  width:  26px;
  height: 26px;
  text-align: center;
  line-height: 20px;
  font-size: 12px;
  opacity: 1;
  box-shadow: 0px 0px 13px 0px rgba(21,237,237,1);
  background: transparent;
  
}

.swiper-pagination-bullet-active {
  background: rgba(21,237,237,1) !important;
}

.content-page.swiper-slide {
  text-align: center;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: unset;
}

.swiper-slide.content-page:not(.swiper-slide-active){
  opacity: 0 !important;
}

.content-page{
  display:  flex;
  width: 60vw;
  opacity: 0;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 0 5em;
  font-family: az2;
  color: white;
}
.content-page__title{
  font-size:  3em;
  font-weight: 450;
}
.content-page__text{
  font-size:  1.2em;
  font-weight: 200;
  margin-bottom: 0;
}
.content-page__span-msg{
  margin-top: 40px;
  font-weight: 150;
  color: #aaaaaa;
}
/*END CONTENT*/